</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String|Object</td>
<td></td>
<td><code v-pre="">required</code> 指定 WMS 服务地址。</td>
</tr>
<tr>
<td>layers</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code> 指定服务图层，多个图层用","隔开。</td>
</tr>
<tr>
<td>parameters</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 在 GetMap URL 中传递给 WMS 服务器的其他参数。</td>
</tr>
<tr>
<td>getFeatureInfoParameters</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 在 GetFeatureInfo URL 中传递给 WMS 服务器的其他参数。</td>
</tr>
<tr>
<td>enablePickFeatures</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定是否支持拾取对象，通过 GetFeatureInfo 获取，需要服务支持。</td>
</tr>
<tr>
<td>getFeatureInfoFormats</td>
<td>Array</td>
<td></td>
<td><code v-pre="">optional</code> 指定 WMS GetFeatureInfo 请求的格式。</td>
</tr>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 WMS 图层矩形范围。 <strong>结构：{ west: number, south: number, east: number, north: number }</strong></td>
</tr>
<tr>
<td>tilingScheme</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 WMS 服务瓦片投影参数。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 WMS 服务椭球体参数，如果指定了 tilingScheme 此属性无效。</td>
</tr>
<tr>
<td>tileWidth</td>
<td>Number</td>
<td><code v-pre="">256</code></td>
<td><code v-pre="">optional</code> 指定像元宽度。</td>
</tr>
<tr>
<td>tileHeight</td>
<td>Number</td>
<td><code v-pre="">256</code></td>
<td><code v-pre="">optional</code> 指定像元高度。</td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定图层可以显示的最小层级。</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定图层可以显示的最大层级，undefined 表示没有限制。</td>
</tr>
<tr>
<td>crs</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> 指定 CRS 规范，用于 WMS 规范&gt; = 1.3.0。</td>
</tr>
<tr>
<td>srs</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> 指定 SRS 规范，用于 WMS 规范 1.1.0 或 1.1.1</td>
</tr>
<tr>
<td>credit</td>
<td>Credit| String</td>
<td></td>
<td><code v-pre="">optional</code> 指定服务描述信息。</td>
</tr>
<tr>
<td>subdomains</td>
<td>String| Array</td>
<td><code v-pre="">'abc'</code></td>
<td><code v-pre="">optional</code> 指定服务子域 。</td>
</tr>
<tr>
<td>clock</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 确定时间维度的值时使用的Clock实例。 当指定options.times时是必需的。</td>
</tr>
<tr>
<td>times</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> TimeIntervalCollection及其数据属性是一个包含时间动态维度及其值的对象。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/WebMapServiceImageryProvider.html">WebMapServiceImageryProvider</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>当图层提供者发生异步错误时触发, 返回一个 TileProviderError 实例。</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>当图层提供者可用时触发, 返回 ImageryProvider 实例。</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          url: 'http://geoserver.nationalmap.nicta.com.au/geotopo_250k/ows',
          layers: 'Hydrography:bores',
          parameters: {
            transparent: true,
            format: 'image/png'
          },
          alpha: 1,
          brightness: 1,
          contrast: 1
        }
      },
      mounted() {
        this.$refs.wms.createPromise.then(({ Cesium, viewer, cesiumObject }) => {
          viewer.camera.setView({
            destination: Cesium.Rectangle.fromDegrees(114.591, -45.837, 148.97, -5.73)
          })
        })
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.cesiumInstance = cesiumInstance
        }
      }
    }
  </script>